/* tslint:disable:no-null-keyword */
/* tslint:disable:max-line-length */
import '@telerik/kendo-theme-default/styles/packages/dropdowns';
import {
    Component,
    forwardRef,
    ElementRef,
    Input,
    Output,
    EventEmitter,
    ContentChild,
    ViewChild,
    OnInit,
    OnDestroy,
    HostBinding,
    isDevMode
} from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { SearchBarComponent } from './searchbar.component';
import { DropDownsUtil as Util } from '@telerik/kendo-dropdowns-common/dist/npm/js/bundle';
import { ItemTemplateDirective } from './templates/item-template.directive';
import { HeaderTemplateDirective } from './templates/header-template.directive';
import { FooterTemplateDirective } from './templates/footer-template.directive';
import { Keys } from './common/keys';
import { IPopupSettings } from './popup-settings';
import { SelectionService, ISelectionEvent } from './selection.service';
import { Subscription } from 'rxjs/Rx';
import { isPresent, isChanged, guid } from './util';

/**
 * @hidden
 */
export const COMBOBOX_VALUE_ACCESSOR: any = {
    multi: true,
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => ComboBoxComponent)
};

/**
 * Represents the Kendo UI ComboBox component for Angular.
 *
 * @example <caption>Basic Usage</caption>
 * ```ts-preview
 * @Component({
 * selector: 'my-app',
 * template: `
 *  <kendo-combobox [data]="listItems">
 *  </kendo-combobox>
 * `
 * })
 * class AppComponent {
 *   public listItems: Array<string> = ["Item 1", "Item 2", "Item 3", "Item 4"];
 * }
 * ```
 */
@Component({
    providers: [COMBOBOX_VALUE_ACCESSOR, SelectionService],
    selector: 'kendo-combobox',
    template: `
        <span #anchor [ngClass]="setHeaderClasses()" unselectable="on">
            <kendo-searchbar #searchbar
                [value]="text"
                (onNavigate)="handleNavigate($event)"
                (valueChange)="handleSearchBarChange($event)"
                (onFilter)="handleFilter($event)"
                (onBlur)="handleBlur($event)"
            ></kendo-searchbar>
            <span
                 [ngClass]="{ 'k-select': true }"
                 (click)="toggle()"
                 (mousedown)="$event.preventDefault()"
                 unselectable="on">
                 <span [ngClass]="{
                     'k-icon': true,
                     'k-i-arrow-s': true
                 }"></span>
        </span>
        <kendo-popup *ngIf="show" [anchor]="anchor" [popupClass]="setListContainerClasses()" [style.width]="getWidth()" (mousedown)="$event.preventDefault()">
            <!--header template-->
            <template *ngIf="headerTemplate"
                [templateContext]="{
                    templateRef: headerTemplate.templateRef
                }">
            </template>
            <!--list-->
            <kendo-list
                [id]="listBoxId"
                [optionPrefix]="optionPrefix"
                [data]="data"
                [textField]="textField"
                [valueField]="valueField"
                [template]="template"
                [height]="getHeight()"
                [show]="show"
            >
            </kendo-list>
            <!--footer template-->
            <template *ngIf="footerTemplate"
                [templateContext]="{
                    templateRef: footerTemplate.templateRef
                }">
            </template>
        </kendo-popup>
  `
})

export class ComboBoxComponent implements ControlValueAccessor, OnInit, OnDestroy {
    public text: any = null;
    public dataItem: any;
    public selected: any[] = [];
    public show: boolean = false;

    private _data: any;
    private _previousValue: any = null;

    /**
     * Sets the data of the ComboBox.
     *
     * > The data has to be provided in an array-like list.
     */
    @Input()
    set data(data: any) {
        this._data = data || [];
    }
    get data(): any {
        return this._data;
    }

    /**
     * Sets the value of the ComboBox. It could be either of the *primivite* (string, numbers)
     * or of the *complex* (objects) type. Use the `valuePrimitive` option to define the type.
     *
     * > Selected values that are not present in the source will be considered as custom values.
     */
    @Input() public value: any;

    /**
     * Sets the data item field that represents the item text.
     *
     * Do not define it if the data contains only primitive values.
     */
    @Input() public textField: string;

    /**
     * Sets the data item field that represents the item value.
     *
     * Do not define it if the data contains only primitive values.
     */
    @Input() public valueField: string;

    /**
     * Specifies the type of the selected value. If set to `true`, the selected value has to be of a *primitive* value.
     *
     * For more details, refer to the section on
     * the [`valuePrimitive`]({% slug overview_combobox_kendouiforangular %}#valueprimitive-boolean) property.
     */
    @Input() public valuePrimitive: boolean;

    /**
     * Configures the popup of the DropDownList. The available options of the `IPopupSettings` are:
     *
     * - `width: Number`&mdash;Sets the width of the popup container. By default will be used the width of the host element.
     * - `height: Number`&mdash;Sets the height of the popup container. By default height is 200px.
     */
    @Input() public popupSettings: IPopupSettings = { height: 200 };

    /**
     * @hidden
     *
     * Enables the auto-completion of the text based on the first data item.
     */
    @Input() public suggest: boolean;

    /**
     * Sets the disabled state of the component.
     */
    @Input() public disabled: boolean;

    /**
     * Specifies the [`tabIndex`](https://developer.mozilla.org/en-US/docs/Web/HTML/Global_attributes/tabindex) of the component.
     */
    @Input() public tabIndex: number = 0;

    /**
     * This event is triggered when the value is changed.
     *
     * For more details, refer to the section on the [`valueChange`]({% slug overview_combobox_kendouiforangular %}#on-value-change) event.
     */
    @Output() public valueChange: EventEmitter<any> = new EventEmitter();

    /**
     * This event is triggered when an item selection is changed.
     *
     * For more details, refer to the section on
     * the [`selectionChange`]({% slug overview_combobox_kendouiforangular %}#on-change-of-item-selection) event.
     */
    @Output() public selectionChange: EventEmitter<any> = new EventEmitter();

    /**
     * This event is triggered when the user types in the input. You can filter the source
     * based on the passed filtration value.
     *
     * For more details, refer to the section on
     * the [`filterChange`]({% slug overview_combobox_kendouiforangular %}#on-filter-change) event.
     */
    @Output() public filterChange: EventEmitter<any> = new EventEmitter();

    @ContentChild(ItemTemplateDirective) protected template: ItemTemplateDirective;
    @ContentChild(HeaderTemplateDirective) protected headerTemplate: HeaderTemplateDirective;
    @ContentChild(FooterTemplateDirective) protected footerTemplate: FooterTemplateDirective;

    @ViewChild(SearchBarComponent) protected searchbar: SearchBarComponent;

    @HostBinding('class.k-widget')
    @HostBinding('class.k-combobox')
    @HostBinding('class.k-header')
    get widgetClasses(): boolean {
        return true;
    }

    @HostBinding('style.max-height') get widgetHeight(): string {
        return this.popupSettings.height + "px";
    }

    @HostBinding('attr.role') get role(): string {
        return "listbox";
    }

    @HostBinding('tabindex') get widgetTabIndex(): number {
        return this.disabled ? undefined : this.tabIndex;
    }

    @HostBinding('attr.aria-disabled') get ariaDisabled(): boolean {
        return this.disabled ? true : undefined;
    }

    @HostBinding('attr.aria-haspopup') get ariaHasPopup(): boolean {
        return true;
    }

    @HostBinding('attr.aria-expanded') get ariaExpanded(): boolean {
        return this.show;
    }

    @HostBinding('attr.aria-owns') get ariaOwns(): string {
        return this.listBoxId;
    }

    @HostBinding('attr.aria-activedescendant') get ariaActivedescendant(): string {
        return this.optionPrefix + "-" + this.value;
    }

    protected listBoxId: string = guid();
    protected optionPrefix: string = guid();

    private selectionService: SelectionService;
    private changeSubscribtion: Subscription;
    private selectSubscribtion: Subscription;

    protected onChangeCallback: Function = (value: any): void => {/* lint metadata */ };
    protected onTouchedCallback: Function = (_: any): void => {/* lint metadata */ };

    private wrapper: any;

    constructor(selectionService: SelectionService, wrapper: ElementRef) {
        this.selectionService = selectionService;
        this.changeSubscribtion = this.selectionService.onChange.subscribe(this.handleItemChange.bind(this));
        this.selectSubscribtion = this.selectionService.onSelect.subscribe(this.handleItemSelect.bind(this));

        this.wrapper = wrapper.nativeElement;
    }

    private handleItemChange(event: ISelectionEvent): void {
        const index: any = event.indices.length ? event.indices[0] : undefined;
        if (!isPresent(index)) { return; }

        this.text = Util.getter(this.data[index], this.textField);
        this.selectionChange.emit(Util.getter(this.data[index], this.valueField));
        this.change(this.data[index]);
    }

    private handleItemSelect(event: ISelectionEvent): void {
        const index: any = event.indices.length ? event.indices[0] : undefined;
        if (!isPresent(index)) { return; }

        this.selectionChange.emit(Util.getter(this.data[index], this.valueField));
        if (!this.show) {
            this.change(this.data[index]);
        }
    }

    public ngOnInit(): void {
        this.verifySettings();
    }

    public ngOnDestroy(): void {
        if (this.changeSubscribtion) {
            this.changeSubscribtion.unsubscribe();
        }

        if (this.selectSubscribtion) {
            this.selectSubscribtion.unsubscribe();
        }
    }

    public ngOnChanges(changes: any): void {
        const dataChanged: boolean = isChanged("data", changes);
        const valueChanged: boolean = isChanged("value", changes);
        if (valueChanged) {
            //only when using [value] binding
            this.verifySettings();
            if (this._previousValue !== this.value) {
                this.text = null;
                this.setState(this.value);
            }
        } else if (dataChanged || changes.data.isFirstChange()) {
            if (this.text == null) {
                this.setState(this.value);
            } else {
                this.search(this.value);
            }
        }
    }

    /**
     * @hidden
     */
    public writeValue(value: any): void {
        this.verifySettings();
        value = Util.getter(value, this.valueField) || value;
        this.text = null;
        this.setState(value);
    }

    /**
     * @hidden
     */
    public registerOnChange(fn: any): void {
        this.onChangeCallback = fn;
    }

    /**
     * @hidden
     */
    public registerOnTouched(fn: any): void {
        this.onTouchedCallback = fn;
    }

    protected verifySettings(): void {
        if (isDevMode()) {
            /*
            if (this.valuePrimitive && isPresent(this.value) && typeof this.value === "object") {
                throw new Error("valuePrimitive binding requires value of primitive type");
            }

            if (!this.valuePrimitive && isPresent(this.value) && typeof this.value !== "object") {
                throw new Error("valuePrimitive binding requires value of primitive type");
            }
            */
        }
    }

    protected setState(value: any): void {
        const newValue: any = Util.getter(value, this.valueField) || value;
        if (!this.data.length) {
            this.value = value;
            return;
        }
        //data and value available - try to resolve initial value to dataItem
        let result: any = Util.resolveValue({
            data: this.data,
            value: newValue,
            valueField: this.valueField
        });
        if (result.dataItem) {
            this.selectionService.resetSelection(result.selected);
            this.dataItem = result.dataItem;
            this.value = newValue;
            this.text = Util.getter(result.dataItem, this.textField);
        } else {
            //no dataItem resolved from this value
            //do we want to display the value as text?
            this.selectionService.resetSelection([]);
            this.value = newValue;
            this.text = null;
            this.dataItem = undefined;
        }
    }

    protected search(value: any): void {
        //TODO: implement search that focuses the first item mathing the value argument
        //filtering AND model update
        let result: any = Util.resolveValue({
            data: this.data,
            value: value,
            valueField: this.valueField
        });
        const resultText: any = Util.getter(result.dataItem, this.textField);
        if (this.text !== resultText) {
            this.selectionService.resetSelection([]);
            this.selectionService.focused = 0;
        }
        this.show = true;
    }

    protected setHeaderClasses(): Object {
        return {
            'k-dropdown-wrap': true,
            'k-state-default': !this.disabled,
            'k-state-disabled': this.disabled
        };
    }

    protected navigate(index: number): void {
        this.text = Util.getter(this.data[index], this.textField);
        this.selectionService.select(index);
    }

    protected handleNavigate(event: any): void {
        const keyCode: number = event.keyCode;
        const max: number = this.data.length - 1;
        const min: number = 0;
        let focused: number = this.selectionService.selected[0];
        let handled: boolean = false;

        if (this.disabled) {
            return;
        }

        if (event.altKey && keyCode === Keys.down) {
            event.preventDefault();
            this.show = true;
        } else if (event.altKey && keyCode === Keys.up) {
            event.preventDefault();
            this.show = false;
        } else if (keyCode === Keys.enter || (keyCode === Keys.esc)) {
            //TODO: should esc trigger change or revert value?
            const isCustom: boolean = focused === undefined;
            this.change(this.data[focused] || this.searchbar.text, isCustom);
        } else if (keyCode === Keys.tab) {
            //release the focus
        } else if (keyCode === Keys.up) {
            if (!isPresent(focused)) {
                focused = max;
            } else {
                focused = focused !== min ? focused - 1 : min;
            }
            handled = true;
        } else if (keyCode === Keys.down) {
            if (!isPresent(focused)) {
                focused = min;
            } else {
                focused = focused !== max ? focused + 1 : max;
            }
            handled = true;
        } else if (keyCode === Keys.home) {
            focused = min;
            handled = true;
        } else if (keyCode === Keys.end) {
            focused = max;
            handled = true;
        }

        if (handled && this.data.length) {
            event.preventDefault();
            this.navigate(focused);
        }
    }

    public handleSearchBarChange(text: string): void {
        this.text = text;
    }

    public handleFilter(text: string): void {
        //Reset the selection prior filter. It will be resolved if there is a match and we don't need it if there isn't one
        this.selectionService.resetSelection([]);
        this.selectionService.focused = 0;
        this.filterChange.emit(text);
    }

    protected handleBlur(): void {
        if (this.text === null) {
            this.show = false;
            return;
        }

        let dataItem: any = this.data[this.selectionService.selected[0]];

        if (Util.getter(dataItem, this.textField) === this.searchbar.value) {
            this.change(dataItem || this.value);
        } else {
            this.change(this.text, true);
        }
        this.show = false;
    }

    protected change(dataItem: any, isCustom: boolean = false): void {
        let customItem: any;
        this.show = false;
        if (isCustom) {
            if (dataItem === this.value) {
                return;
            } else {
                customItem = {
                    [this.textField]: dataItem,
                    [this.valueField]: undefined
                };
            }
        } else if (this.dataItem === dataItem) {
            return;
        }

        this.dataItem = isCustom ? customItem : dataItem;
        this.value = Util.getter(this.dataItem, this.valueField) || dataItem;
        this._previousValue = this.value;

        this.onChangeCallback(this.valuePrimitive ? this.value : dataItem);
        this.valueChange.emit(this.valuePrimitive ? this.value : dataItem);
    }

    protected toggle(): void {
        //TODO: add data.length/total check
        if (!this.disabled) {
            this.searchbar.focus();
            this.show = !this.show;
            if (this.show) {
                this.filterChange.emit("");
            }
        }
    }

    protected getWidth(): any {
        return this.popupSettings.width || this.wrapper.style.width || this.wrapper.offsetWidth + "px";
    }

    protected getHeight(): any {
        //header, footer ???
        return this.popupSettings.height;
    }

    protected setListContainerClasses(): Object {
        return {
            'k-list-container': true,
            'k-reset': true
        };
    }
}



